.about-header{
    position: relative;
    width: 100%;
    height: 150px;
    padding-left: 1rem;
    margin-top: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.about-header h1{
    font-family: Train One ;
    position: relative;
    font-size: 350%;
    z-index: 2;
    color: var(--first-color);
}
#abt-h1{
    position: absolute;
    font-size: 450%;
    color: var(--high-light-color);
    z-index: 1;
    opacity: 0.3
}

.ab-container{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.ab-con-left{
    width: 40%;

}
.ab-model{
    padding: 7px;
    object-fit: cover;
    border-radius: 50%;
    width: 60%;
    height: 60%;
    border: 7px dashed transparent;
    outline: 10px double var(--high-light-color);
    outline-offset: 0.5rem;
    animation: borderAnimation 4s linear infinite alternate;
}
@keyframes borderAnimation {
    0% {
        border-color:var(--high-light-color) var(--shadow-color) var(--shadow-color) var(--shadow-color);
        border-style: dashed;
        outline: 10px dashed var(--shadow-color);
    }
    25% {
        border-color: var(--shadow-color) var(--high-light-color) var(--shadow-color) var(--shadow-color);
        border-style: dashed;
        outline: 10px dashed var(--high-light-color);
    }
    50% {
      border-color:  var(--shadow-color) var(--shadow-color) var(--high-light-color) var(--shadow-color);
      border-style: dashed;
      outline: transparent;
    }
    75%{
        border-color: var(--shadow-color) var(--shadow-color) var(--shadow-color) var(--high-light-color);
        border-style: dashed;
        outline: 10px dotted var(--shadow-color);
    }
    100%{
        border-color: var(--high-light-color) var(--shadow-color) var(--shadow-color) var(--shadow-color);
        border-style: dashed;
        outline: 10px dotted var(--high-light-color);
    }
}


.ab-con-right{
    width: 50%;
}
.about-header span{
    color: var(--high-light-color);
}
.ab-con-right strong{
    color: var(--high-light-color);
}

.contact-container{
    margin-top: 2rem;
}
.contact-details{
    margin-top: 1rem;
}



@media  screen and (max-width: 750px) {
    .about-header h1{
        font-size: 300%;
    }
    #abt-h1{
        font-size: 400%;
    }
    .ab-container{
        flex-direction: column;
    }
    .ab-con-left{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .ab-model{
        padding: 5px;
        width:50%;
        height: 50%;
        border-radius: 50%;
        outline: 5px solid var(--shadow-color);
    }

    .ab-con-right{
        padding: 4rem 4rem 0 4rem;
        width: 100%;
    }
    
}