.experience-header{
    position: relative;
    width: 100%;
    height: 150px;
    padding-left: 1rem;
    margin-top: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.experience-header h1{
    font-family: Train One ;
    position: relative;
    font-size: 350%;
    z-index: 2;
    color: var(--first-color);
}
#exp-h1{
    position: absolute;
    font-size: 450%;
    color: var(--high-light-color);
    z-index: 1;
    opacity: 0.3
}



.exp-container{
    padding-left: 4rem;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: start;
}

.exp-container h2,h4,h5{
    color: var(--high-light-color);
}
.exp-container span{
    color: var(--first-color);
}

.exp-container .exp-list{
    padding: 1rem 2rem;
}
.exp-list1 li{
    display: flex;
    align-items: center;
}
.exp-list1 li span{
    padding-left: 8px;
    padding-right: 8px;
    color: var(--high-light-color);
}

.exp-list2 li{
    list-style-type: circle;
}

.telecom-works{
    padding-left: 2rem;
    padding-top: 2rem;
}

@media screen and (max-width: 750px) {
    .experience-header h1{
        font-size: 300%;
    }
    #exp-h1{
        font-size: 380%;
    }
    .exp-container{
        width: 100%;
    }

    .telecom-works{
        width: 100%;
    }
    .exp-list1 li{
        display: inline-block;
    }
}
