.footer {
    width: 100%;
    background-color: rgb(33, 44, 63);
    color: var(--title-color);
    font-size: 0.9rem;
  }
  
  .footer-container {
    display: flex;
    justify-content:space-evenly;
    align-items: start;
    flex-wrap: wrap;
  }
  
  .footer-column h4 {
    color: #fff;
    margin-bottom: 0.5rem;
  }
  
  .footer-links ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: start;
    align-items: start;
  }
  
  .footer-links ul li {
    margin: 0 15px;
    font-size: 1rem;
  }
  
  .footer-links ul li a {
    color: #b0b0b0;
    text-decoration: none;
    transition: color 0.3s;
  }
  
  .footer-links ul li a:hover {
    color: #fff;
  }
  
  .footer-right {
    text-align: right;
  }
  
  .footer-right p {
    margin: 0.2rem 0;
  }
  
  @media screen and (max-width: 750px){
    .footer-container{
      margin-left: 2rem;
      flex-direction: column;
      justify-content:center;
    }
  }