.edu-header{
    position: relative;
    width: 100%;
    height: 150px;
    padding-left: 1rem;
    margin-top: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.edu-header h1{
    font-family: Train One ;
    position: relative;
    font-size: 350%;
    z-index: 2;
    color: var(--first-color);
}
#edu-h1{
    position: absolute;
    font-size: 450%;
    color: var(--high-light-color);
    z-index: 1;
    opacity: 0.3
}

.edu-list{
    margin-top: 2rem;
    margin-left: 6rem;
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: column;
}

.edu-li-item{
    
    padding: 1rem 2rem;
    display:flex;
    justify-content: start;
    align-items: center;
    width: 100%;
    height: 250px;
}
.edu-item{
    padding-right: 2rem;
    background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.2),
    rgba(255, 255, 255, 0.1)
  );
  border: 1px solid rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(2px);
    -webkit-backdrop-filter: blur(20px);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: center;
    align-items: end;
}
.c-img{
    width: 40%;
    height: 280px;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.c-img-item{
    width: 336px;
    height: 224px;
    border: 2px solid var(--first-color);
    padding: 5px;
    border-radius: 30px;
    transition: transform 0.8s cubic-bezier(0.25, 0.8, 0.25, 1);

}
.c-img-item:hover{
    opacity: 0.8;
    box-shadow: 0 0 15px var(--shadow-color), 0 0 15px var(--shadow-color);
    transform: scale(1.2);
}
.course{
    color: var(--hover-color);
}
.course-date{
    color: var(--hover-color);
}
.edu-list h2{
    padding-left: 2rem;
    color: var(--first-color);
}
.edu-item h4{
    color: var(--high-light-color);
}

@media screen and (max-width: 750px){
    .edu-header{
        margin-left: 0;
    }
    .edu-header h1{
        font-size: 300%;
    }
    #edu-h1{
        font-size: 400%;
    }
    .edu-list{
        margin-left: 4rem;
    }

    .edu-li-item{
        padding-left: 0;
    }

    .edu-item{
        padding-right: 0;
        align-items: center;
        width: 100%;
    }
    .edu-li-item .c-img{
        display: none;
    }
}